.main-container {
	position: sticky;
	top: 0;
	width: 100%;
	display: flex;
	justify-content: space-evenly;
	border-bottom: 0.1px solid #ccc;
	background-color: white;
	z-index: 100;
}

.left-container {
	display: flex;
}

.logo-image {
	object-fit: contain;
	margin: 0.2rem 1rem 0 0;
}

.search-bar {
	display: flex;
	align-items: center;
	background-color: #eef3f8;
	border-radius: 10px;
	padding: 0.5rem;
	margin: 0.5rem 2rem 0.5rem 0;
	width: 12.5rem;
}

.search-icon {
	color: gray;
}

.search-bar > input {
	outline: none;
	border: none;
	background: none;
	margin-left: 0.5rem;
	font-weight: normal;
	width: 100%;
}

.icon-list {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.logout-responsive {
	display: none;
}

@media (max-width: 768px) {
	.logout-responsive {
		cursor: pointer;
		display: block;
		position: absolute;
		right: 10px;
		top: 30%;
	}
	.right-container {
		display: none;
	}
	.search-bar {
		display: none;
	}

	.logo {
		display: flex;
		align-items: center;
		justify-content: center;
		font-family: "Open Sans";
	}
	.logo-image {
		width: 150px;
		height: 60px;
	}
}
